@import url("https://fonts.googleapis.com/css2?family=DM+Sans&display=swap");
html {
  display: flex;
  justify-content: center;
}

body {
  font-family: DM Sans, sans-serif;
  max-width: 1440px;
  width: 100%;
}
body button {
  background-color: transparent;
  border: 0;
  color: #901c1c;
  cursor: pointer;
  padding: 0;
}

.photographerLink {
  width: 12.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.photographerLink .imgContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.5rem;
  height: 12.5rem;
  margin-bottom: 1.25rem;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0px 5px 15px 5px rgba(0, 0, 0, 0.2);
}
.photographerLink .imgContainer img {
  position: relative;
  max-width: none;
}
.photographerLink .imgContainer .mimi {
  width: 19rem;
  top: 1.0625rem;
}
.photographerLink .imgContainer .ellie {
  width: 13.5625rem;
  left: 0.1875rem;
  top: 0.25rem;
}
.photographerLink .imgContainer .tracy {
  width: 32.375rem;
  top: 1.6875rem;
}
.photographerLink .imgContainer .nabeel {
  width: 32.875rem;
  top: 1.8125rem;
}
.photographerLink .imgContainer .rhode {
  width: 33.1875rem;
  top: 13.1875rem;
  left: 2.25rem;
}
.photographerLink .imgContainer .marcel {
  width: 33.75rem;
  top: 1.75rem;
}
.photographerLink h2 {
  color: #d3573c;
  font-size: 2.25rem;
  white-space: nowrap;
}

.btContact {
  background-color: #901c1c;
  color: white;
  font-weight: bold;
  border-radius: 5px;
}
.btContact-mobile {
  width: 10.625rem;
  height: 2.1875rem;
  font-size: 1.125rem;
  position: fixed;
  bottom: 1.875rem;
  left: 50vw;
  transform: translateX(-50%);
}
@media screen and (min-width: 992px) {
  .btContact-mobile {
    display: none;
  }
}
.btContact-desktop {
  display: none;
}
@media screen and (min-width: 992px) {
  .btContact-desktop {
    position: absolute;
    left: 28.125rem;
    top: 3.9375rem;
    width: 10.625rem;
    height: 4.3125rem;
    font-size: 1.25rem;
    display: block;
  }
}

.filterLink {
  border: 0.5px solid #c4c4c4;
  border-radius: 0.6875rem;
  margin: 0 0.125rem;
  padding: 0.125rem 0.375rem;
}
.filterLink:focus, .filterLink:hover {
  background-color: #901c1c;
  color: #fff;
}
@media screen and (min-width: 992px) {
  .filterLink {
    margin: 0.125rem;
  }
}

.logo {
  align-items: center;
  display: flex;
  grid-area: logo;
  height: 109px;
}
@media screen and (min-width: 992px) {
  .logo {
    height: 137px;
  }
  .logo img {
    fill: none;
    height: 50px;
    width: 200px;
  }
}

.indexArticle {
  flex: 1 0 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 25%;
  min-width: 23.125rem;
  margin-bottom: 1.8125rem;
}
@media screen and (min-width: 992px) {
  .indexArticle {
    margin-bottom: 3rem;
  }
}
.indexArticle p {
  margin: 1.5px 0;
}
.indexArticle .location {
  font-size: 0.8125rem;
  color: #901c1c;
  line-height: 0.9375rem;
}
.indexArticle .tagline {
  font-size: 0.625rem;
}
.indexArticle .price {
  color: #757575;
  font-size: 0.5625rem;
  margin-bottom: 0.375rem;
}

nav {
  align-content: space-between;
  display: flex;
  flex-wrap: wrap;
  font-size: 0.75rem;
  grid-area: nav;
  justify-content: center;
  margin: 0 3rem 0 2.25rem;
}
@media screen and (min-width: 992px) {
  nav {
    align-content: center;
    margin: 0;
  }
}

html {
  display: flex;
  justify-content: center;
}

header {
  display: grid;
  grid-template-areas: "logo p" "nav nav";
  grid-template-columns: 50% 50%;
  grid-template-rows: 6.8125rem 3.125rem;
  margin-bottom: 2.8125rem;
}
header .logo {
  margin-left: 1.38rem;
}
@media screen and (min-width: 992px) {
  header {
    grid-template-areas: "logo nav p";
    grid-template-columns: auto;
    grid-template-rows: 8.25rem;
    margin-bottom: 4.5rem;
  }
  header .logo {
    margin-left: 7.3rem;
    height: 100%;
  }
}
header .photographersLink {
  align-items: center;
  display: flex;
  font-size: 0.875rem;
  grid-area: p;
  justify-content: flex-end;
  margin-right: 1.38rem;
}
@media screen and (min-width: 992px) {
  header .photographersLink {
    font-size: 2.25rem;
    margin-right: 7.3rem;
  }
}

main {
  display: flex;
  flex-wrap: wrap;
}